@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #0d182a;
  color: rgb(236, 193, 97);
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(#e66465, #9198e5);
}

.coin-input::placeholder {
  color: #0d182a;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin h1{
  color:white
}

.coin h1:hover{
color: rgb(255, 238, 0);
}